<script>
import componentVue from "../caseConfig/component.vue";
export default {
  data() {
    return {
      openLoading:false,
      resetLessonLoading:false,
      createBuyLessonQRCodeInfo: {
        env: 'test'
      },
      buyLessonQRCodeDialog: false,
      projectList: [],
      utilsList: [],
      fenye: {
        pageSize: 10,
        pageNum: 1
      },
      openLessonDialog: false,
      openLessonInfo: {
        value: '',
        type:'passportId',
        lessonPackageId: "",
        termScheduleId: ""
      },
      lessonPackageList: [],
      termScheduleList: [],
      UserLessonInfo: [],
      resetLessonDialog: false,
      resetLessonInfo: {
        passportId: "",
        termScheduleCode: "",
        lessonCode: ""
      },
      usesLessonInfo: [],
      openLessonRules: {
        value: [
          { required: true, message: "", trigger: "blur" }
        ],
        lessonPackageId: [
          { required: true, message: "", trigger: "change" }
        ],
        termScheduleId: [
          { required: true, message: "", trigger: "change" }
        ]
      },
      resetLessonRules: {
        passportId: [
          { required: true, message: "passportId", trigger: "blur" }
        ]
      },
      buyLessonQRCodeRules: {
        passportId: [
          { required: true, message: "passportId", trigger: "blur" }
        ]
      }
      ,buyLessonQRCodeSrc:''
    };
  },
  methods: {
     async makeBuyLessonQRCode(){
      console.log(this.createBuyLessonQRCodeInfo);
      const url = "https://api.qrserver.com/v1/create-qr-code/?size=150x150&data=https://www.confucius.mobi/qw_goods/view/goodsDetail?goodsCode=" + this.createBuyLessonQRCodeInfo.goodsCode;
      const response = await this.$common.get('/QRCode/getBuyLessonCode',this.createBuyLessonQRCodeInfo);
      console.log(response);
    },
    closebuyLessonQRCodeDialog() {
      this.buyLessonQRCodeDialog = false;
    },
    async test() {
      const response = await this.$common.get("/userInfo/test");
      console.log(response);
    },
    // 
    closeResetLessonDialog() {
      this.resetLessonDialog = false;
      this.usesLessonInfo = [];
      this.$refs.resetLessonPassportIdRef.resetFields();
    },
    // 
    async restLessonConfirm(termScheduleCode, lessonCode, lessonName) {
      await this.$confirm(
        "[" +
          this.resetLessonInfo.passportId +
          "]" +
          lessonName +
          "?",
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        }
      )
        .then(() => {
          this.restLesson(termScheduleCode, lessonCode);
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    },
    // 
    async restLesson(termScheduleCode, lessonCode) {
      this.resetLessonInfo.termScheduleCode = termScheduleCode;
      this.resetLessonInfo.lessonCode = lessonCode;
      const response = await this.$common.get(
        "/classMemberRegistration/resetLesson",
        this.resetLessonInfo
      );
      if (response.code == 10000) {
        this.$message.success(response.msg);
      } else {
        this.$message.error(response.msg);
      }
    },
    // 
    async getAllUtils() {
      const response = await this.$common.get(
        "/quanwaiUtils/getAllUtils",
        this.fenye
      );
      if (response.code === 10000) {
        this.utilsList = response.data;
      } else {
        this.$message.error(response.msg);
      }
    },
    // pathdialog
    openDialog(path) {
      if (path == "openLesson") {
        this.goOpenLessonDialog();
      } else if (path == "resetLesson") {
        this.goResetLessonDialog();
      } else if (path == "syncAccessToken") {
        this.goSyncAccessToken();
      } else if (path == "createBuyLessonQRCode") {
        // 
        this.openCreateBuyLessonQRCode();
      }
    },
    // Dialog
    openCreateBuyLessonQRCode() {
      this.buyLessonQRCodeDialog = true;
    },
    // dialog
    async goOpenLessonDialog() {
      this.openLessonDialog = true;
      const response = await this.$common.get("/lessonPackage/getAllPackage");
      this.lessonPackageList = response.data;
    },
    // dialog
    goResetLessonDialog() {
      this.resetLessonDialog = true;
    },
    // 
    async getUserLesson() {
      this.resetLessonLoading = true;
      const response = await this.$common.get(
        "/classMemberRegistration/getUserLessonByPassportId?passportId=" +
          this.resetLessonInfo.passportId
      );
      this.usesLessonInfo = response.data;
      this.resetLessonLoading = false;
    },
    // dialog
    closeOpenLessonDialog() {
      this.openLessonDialog = false;
      this.openLoading = false;
      this.$refs.openLessonInfoRef.resetFields();
    },
    // 
    async selectLessonPackage() {
      // 
      const response = await this.$common.get(
        "/termSchedule/getTermSchedule?lessonPackageId=" +
          this.openLessonInfo.lessonPackageId
      );
      this.termScheduleList = response.data;
    },
    // 
    async openLesson() {
      
      await this.$refs.openLessonInfoRef.validate(valid => {
        if (valid) {
          this.openLessonPost();
        } else {
          return false;
        }
      });
    },
    async openLessonPost() {
      this.openLoading = true;
      const response = await this.$common.post(
        "/classMemberRegistration/openLesson",
        this.openLessonInfo
      );
      if (response.code == 10000) {
        this.openLoading = false;
        this.$message.success(response.msg);
        this.closeOpenLessonDialog();
      } else {
        this.openLoading = false;
        // this.$message.error(response.msg);
      }

    },
    // 
    clearDialog() {
      console.log("");
    },
    // access_token
    async goSyncAccessToken() {
      await this.$confirm(
        "access_token",
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        }
      )
        .then(() => {
          this.syncAccessToken();
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    },
    // token
    async syncAccessToken() {
      const response = await this.$common.get("/accessToken/sync");
      if (response.code == 10000) {
        this.$message.success(response.msg);
      } else {
        this.$message.error(response.msg);
      }
    }
  },

  created() {
    // 
    this.getAllUtils();
    
  }
};
</script>
