<template>
  <div v-loading="loading" element-loading-text="...">
    <!--  -->
    <el-breadcrumb>
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <!-- or -->
      <el-row :gutter="10">
        <!--  -->
        <el-col :span="3">
          <el-select clearable v-model="queryCaseListParams.protocolId" placeholder="">
            <el-option
              v-for="item in protocolsOptions"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-col>
        <!--  -->
        <el-col :span="3" v-if="this.currentProjectId == ''">
          <el-select clearable v-model="queryCaseListParams.projectId" placeholder="">
            <el-option
              v-for="item in projectOptions"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-col>

        <!--  -->
        <el-col :span="3" v-else>
          <el-select clearable disabled v-model="currentProjectName" placeholder=""></el-select>
        </el-col>

        <el-col :span="4">
          <el-input
            @keyup.enter.native="getCaseList()"
            placeholder="Case"
            v-model="queryCaseListParams.query"
            clearable
            @clear="getCaseList()"
          ></el-input>
        </el-col>
        <el-col :span="2">
          <el-button icon="el-icon-search" @click="getCaseListBySelect()"></el-button>
        </el-col>
        <el-col :span="12" style="text-align:right">
          <!-- <el-button type="primary"></el-button> -->
          <el-button type="primary" @click="toAddCaseAction()">Case</el-button>
        </el-col>
      </el-row>

      <!--  -->
      <el-table :data="caseList" border height="650" style="width: 100%">
        <el-table-column fixed type="index" label="#" width="40px"></el-table-column>
        <el-table-column prop="name" label="" width="190">
          <template slot-scope="scope">
            <el-popover placement="right" width="400px" trigger="hover">
              <el-card class="box-card">
                <div>
                  <span></span>
                  <span>{{scope.row.desc != null ?scope.row.desc : "" }}</span>
                </div>
              </el-card>
              <div slot="reference" style="cursor:pointer">{{scope.row.name}}</div>
            </el-popover>
          </template>
        </el-table-column>
        <el-table-column prop="projectName" label="" width="150">
          <template slot-scope="scope">
            <el-tag type="warning">{{scope.row.projectName}}</el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="protocolId" label="" width="90">
          <template slot-scope="scope">
            <el-tag v-if="scope.row.protocolId==1" type="success">GET</el-tag>
            <el-tag v-if="scope.row.protocolId==2" type="danger">POST</el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="url" label="url" width="250"></el-table-column>
        <!-- <el-table-column prop="getParams" label="" width="300"></el-table-column> -->
        <el-table-column prop="assertFlag" label="" width="180">
          <template slot-scope="scope">
            <span :key="index" v-for="(item,index) in scope.row.assertFlag.split('<==>')">
              {{item}}
              <br />
            </span>
          </template>
        </el-table-column>
        <el-table-column prop="assertContent" label="" width="180">
          <template slot-scope="scope">
            <span :key="index" v-for="(item,index) in scope.row.assertContent.split('<==>')">
              {{item}}
              <br />
            </span>
          </template>
        </el-table-column>
        <el-table-column prop="preCaseGroup" label="" min-width="250"></el-table-column>
        <!--  -->
        <el-table-column fixed="right"  label="" width="70">
          <template slot-scope="scope">
            <el-tooltip
              v-if="scope.row.del"
              class="item"
              effect="dark"
              content=""
              placement="top"
              :enterable="false"
            >
              <el-switch
                v-if="scope.row.del"
                v-model="scope.row.del"
                @change="changeDel(scope.row)"
              ></el-switch>
            </el-tooltip>
            <el-tooltip
              v-if="!scope.row.del"
              class="item"
              effect="dark"
              content=""
              placement="top"
              :enterable="false"
            >
              <el-switch
                v-if="!scope.row.del"
                v-model="scope.row.del"
                @change="changeDel(scope.row)"
              ></el-switch>
            </el-tooltip>
          </template>
        </el-table-column>
        <!--  -->
        <el-table-column fixed="right"  label="" width="240">
          <template slot-scope="scope">
            <el-button
              v-if="scope.row.del"
              type="primary"
              icon="el-icon-edit-outline"
              @click="toEditCaseAction(scope.row)"
              size="mini"
            ></el-button>
            <el-button
              v-if="scope.row.del"
              type="primary"
              icon="el-icon-video-play"
              size="mini"
              @click="runCaseById(scope.row.id)"
            ></el-button>

            <el-tooltip
              class="item"
              effect="dark"
              content=""
              placement="top"
              :enterable="false"
            >
              <el-button size="mini" icon="el-icon-view" @click="getExecuteRecoding(scope.row.id)"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>

      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryCaseListParams.pageNum"
        :page-sizes="[10, 15, 20, 50]"
        :page-size="queryCaseListParams.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="caseCount"
      ></el-pagination>
    </el-card>

    <el-dialog
  title="" :close-on-click-modal="false"
  :visible.sync="executeResponseDialog"
  width="80%"
  >
  <el-form  label-width="80px" :model="caseResult">
  <el-form-item label="">
    <el-input contenteditable="true" type="textarea"  rows=20 v-model="caseResult.responseStr"></el-input>
  </el-form-item>
  <el-form-item label="">
    <el-table
      :data="caseResult.assertDomains"
      style="width: 100%">
      <el-table-column
        prop="nameValue"
        label=""
        width="300">
      </el-table-column>
      <el-table-column
        prop="paramValue"
        label=""
        width="300">
      </el-table-column>
            <el-table-column
        prop="paramDesc"
        label="">
      </el-table-column>
    </el-table>
  </el-form-item>
  <el-form-item label="">
    <el-table
      :data="caseResult.paramsDomains"
      style="width: 100%">
      <el-table-column
        prop="nameValue"
        label=""
        width="300">
      </el-table-column>
      <el-table-column
        prop="paramValue"
        label=""
        width="300">
        </el-table-column>
              <el-table-column
        prop="paramDesc"
        label=""
        >
        </el-table-column>
    </el-table>
  </el-form-item>
</el-form>

  <span slot="footer" class="dialog-footer">
    <el-button type="primary" @click="executeResponseDialog = false"></el-button>
  </span>
</el-dialog>

  </div>
</template>
<script>
export default {
  data () {
    return {
      drawer: false,
      // 
      loading: false,
      // 
      textareaHigh: 5,
      // 
      protocolsOptions: [],
      // 
      projectOptions: [],
      // 
      queryCaseListParams: {
        query: '',
        pageNum: 1,
        pageSize: 10,
        protocolId: '',
        projectId: ''
      },
      // 
      delCaseParam: {
        cid: 0,
        flag: true
      },
      // 
      caseList: [],
      // 
      caseCount: 0,
      caseGroupList: {},
      queryForm: {
        query: '',
        pageNum: 1,
        pageSize: 50
      },
      currentProjectId: '',
      currentProjectName: '',
      executeResponseDialog: false,
      caseResult: {}
    }
  },
  methods: {
    toEditCaseAction (row) {
      this.$router.push('/case/single/action/' + row.id)
    },
    toAddCaseAction () {
      //  
      this.$router.push('/case/single/action/add')
    },
    // 
    getCaseListBySelect () {
      this.queryCaseListParams.pageNum = 1
      // this.queryCaseListParams.pageSize = 10;
      this.getCaseList()
    },
    // 
    async getCaseList () {
      const { data: response } = await this.$http.get('/getAllCase', {
        params: this.queryCaseListParams
      })
      if (response.code != 10000) {
        this.caseList = response.data
        this.$message.error(response.msg)
      } else {
        this.caseList = response.data
        this.caseCount = response.caseCount
      }
    },
    // 
    async delCase (row) {
      this.delCaseParam.cid = row.id
      this.delCaseParam.flag = row.del
      const { data: response } = await this.$http.get('/toDelCase', {
        params: this.delCaseParam
      })
      if (response.code != 10000) {
        this.$message.error('')
      }
      if (!row.del) {
        this.$message({
          type: 'success',
          message: '!'
        })
      } else {
        this.$message({
          type: 'success',
          message: '!'
        })
      }
      this.getCaseList()
    },
    // 
    async changeDel (row) {
      // 
      if (!row.del) {
        await this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        })
          .then(() => {
            this.delCase(row)
          })
          .catch(() => {
            this.getCaseList()
            this.$message({
              type: 'info',
              message: ''
            })
          })
      } else {
        await this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        })
          .then(() => {
            this.delCase(row)
            // console.log("");
          })
          .catch(() => {
            this.getCaseList()
            this.$message({
              type: 'info',
              message: ''
            })
          })
      }
    },
    // 
    handleSizeChange (newSize) {
      this.queryCaseListParams.pageSize = newSize
      this.getCaseList()
      // console.log(newSize);
    },
    // 
    handleCurrentChange (newPage) {
      this.queryCaseListParams.pageNum = newPage
      this.getCaseList()
      // console.log(newPage);
    },
    // 
    closeAdd () {
      this.$refs.addCaseFormRef.resetFields()
      this.textareaHigh = 4
    },
    // 
    async getProtocol () {
      const { data: response } = await this.$http.get('/getAllProtocol')
      this.protocolsOptions = response.data
    },
    // 
    async getProject () {
      this.$common.getAllProject().then(data => {
        this.projectOptions = data
      })
    },
    // 
    async runCaseById (id) {
      this.loading = true
      const { data: response } = await this.$http.get('/runCaseById?id=' + id)
      this.loading = false
      if (response.code == 10000) {
        this.$message.success(response.msg)
        this.caseResult = response.params
        this.executeResponseDialog = true
      } else {
        this.$message.error(response.msg)
        this.caseResult = response.params
        this.executeResponseDialog = true
      }
    },
    async getExecuteRecoding (cid) {
      this.$router.push('/case/executeRecoding/' + cid)
    },
    changeProject (id, name) {
      window.sessionStorage.setItem('projectId', id)
      window.sessionStorage.setItem('projectName', name)
      location.reload()
    },
    delProject () {
      window.sessionStorage.removeItem('projectId')
      window.sessionStorage.removeItem('projectName')
      location.reload()
    }
  },
  created () {
    if (window.sessionStorage.getItem('projectId') != null) {
      this.queryCaseListParams.projectId = window.sessionStorage.getItem(
        'projectId'
      )
      this.currentProjectName = window.sessionStorage.getItem('projectName')
      this.currentProjectId = window.sessionStorage.getItem('projectId')
    }
    this.getCaseList()
    this.getProtocol()
    this.getProject()
  },
  beforeRouteLeave (to, from, next) {
    to.meta.keepAlive = false
    next()
  }
}
</script>
<style lang="less" scoped></style>
