    </el-breadcrumb>
    <!--  -->
    <el-card>
      <!-- or -->
      <el-row :gutter="10">
        <el-col :span="4">
          <el-input
            @keyup.enter.native="getUserList()"
            placeholder=""
            v-model="queryUserListParams.query"
            clearable
            @clear="getUserList()"
          >
            <el-button slot="append" icon="el-icon-search" @click="getUserList()"></el-button>
          </el-input>
        </el-col>
        <el-col :span="20" style="text-align:right">
          <!-- <el-button type="primary"></el-button> -->
          <el-button type="primary" @click="addDialogVisible = true"></el-button>
        </el-col>
      </el-row>

      <!--  -->
      <el-table :data="userList" border style="width: 100%">
        <el-table-column fixed type="index" label="#" width="50px"></el-table-column>
        <el-table-column prop="username" label="" width="150"></el-table-column>
        <el-table-column prop="customerName" label="" width="150"></el-table-column>
        <el-table-column prop="mobile" label="" width="150"></el-table-column>
        <el-table-column prop="email" label="" width="200"></el-table-column>
        <el-table-column prop="address" label="" width="300"></el-table-column>
        <el-table-column prop="company" label="" width="300"></el-table-column>
        <el-table-column prop="department" label="" width="200"></el-table-column>
        <!--  -->
        <el-table-column fixed="right" label="" width="70">
          <template slot-scope="scope">
            <el-tooltip
              v-if="scope.row.del"
              class="item"
              effect="dark"
              content=""
              placement="top"
              :enterable="false"
            >
              <el-switch v-model="scope.row.del" @change="changeDel(scope.row)"></el-switch>
            </el-tooltip>
            <el-tooltip
              v-else
              class="item"
              effect="dark"
              content=""
              placement="top"
              :enterable="false"
            >
              <el-switch v-model="scope.row.del" @change="changeDel(scope.row)"></el-switch>
            </el-tooltip>
          </template>
        </el-table-column>
        <!--  -->
        <el-table-column fixed="right" label="" width="180">
          <template slot-scope="scope">
            <el-button
              type="primary"
              icon="el-icon-edit-outline"
              @click="showDialogVisible(scope.row)"
              size="mini"
            ></el-button>
            <!-- </el-tooltip> -->

            <el-button
              type="primary"
              icon="el-icon-user"
              size="mini"
              @click="getUserInfo(scope.row.id)"
            ></el-button>
            <!-- </el-tooltip> -->
          </template>
        </el-table-column>
      </el-table>

      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryUserListParams.pageNum"
        :page-sizes="[10, 15, 20, 50]"
        :page-size="queryUserListParams.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="userTotal"
      ></el-pagination>

    </el-card>

    <!--  -->
    <el-dialog :close-on-click-modal="false" title="" :visible.sync="addDialogVisible" width="50%" @close="closeAdd()">
      <span></span>
      <!--  -->
      <el-form ref="addFormRef" :rules="addFormRules" :model="addForm" label-width="70px">
        <el-form-item
          style="margin-left:150px;margin-right:150px"
          class="input-center"
          label=""
          prop="username"
        >
          <el-input
            prefix-icon="el-icon-user"
            v-model="addForm.username"
            placeholder=""
            style="width:93%"
          ></el-input>
        </el-form-item>
        <el-form-item
          style="margin-left:150px;margin-right:150px"
          class="input-center"
          label=""
          prop="customerName"
        >
          <el-input
            prefix-icon="el-icon-user-solid"
            v-model="addForm.customerName"
            placeholder=""
            style="width:93%"
          ></el-input>
        </el-form-item>
        <el-form-item style="margin-left:150px;margin-right:150px" label="" prop="password">
          <el-input type="password"
            prefix-icon="el-icon-key"
            v-model="addForm.password"
            placeholder=""
            style="width:93%"
          ></el-input>
        </el-form-item>
        <el-form-item style="margin-left:150px;margin-right:150px" label="" prop="mobile">
          <el-input
            prefix-icon="el-icon-mobile-phone"
            v-model="addForm.mobile"
            placeholder=""
            style="width:93%"
          ></el-input>
        </el-form-item>
        <el-form-item style="margin-left:150px;margin-right:150px" label="" prop="email">
          <el-input
            prefix-icon="el-icon-message"
            v-model="addForm.email"
            placeholder=""
            style="width:93%"
          ></el-input>
        </el-form-item>
        <el-form-item style="margin-left:150px;margin-right:150px" label="" prop="address">
          <el-input
            prefix-icon="el-icon-map-location"
            v-model="addForm.address"
            placeholder=""
            style="width:93%"
          ></el-input>
        </el-form-item>
        <el-form-item style="margin-left:150px;margin-right:150px" label="" prop="company">
          <el-input
            prefix-icon="el-icon-office-building"
            v-model="addForm.company"
            placeholder=""
            style="width:93%"
          ></el-input>
        </el-form-item>
        <el-form-item style="margin-left:150px;margin-right:150px" label="" prop="department">
          <el-input
            prefix-icon="el-icon-monitor"
            v-model="addForm.department"
            placeholder=""
            style="width:93%"
          ></el-input>
        </el-form-item>
      </el-form>
      <!--  -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addUser()"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog :close-on-click-modal="false"  title="" :visible.sync="editDialogVisible" width="50%" @close="closeEdit()">
      <span></span>
      <!--  -->

      <el-form ref="editFormRef" :rules="addFormRules" :model="editForm" label-width="70px">
        <el-form-item
          style="margin-left:150px;margin-right:150px"
          class="input-center"
          label=""
          prop="username"
        >
          <el-input
            prefix-icon="el-icon-user"
            v-model="editForm.username"
            placeholder=""
            disabled
            style="width:93%"
          ></el-input>
        </el-form-item>
        <el-form-item
          style="margin-left:150px;margin-right:150px"
          class="input-center"
          label=""
          prop="customerName"
        >
          <el-input
            prefix-icon="el-icon-user-solid"
            v-model="editForm.customerName"
            placeholder=""
            style="width:93%"
          ></el-input>
        </el-form-item>
        <el-form-item style="margin-left:150px;margin-right:150px" label="" prop="mobile">
          <el-input
            prefix-icon="el-icon-mobile-phone"
            v-model="editForm.mobile"
            placeholder=""
            style="width:93%"
          ></el-input>
        </el-form-item>
        <el-form-item style="margin-left:150px;margin-right:150px" label="" prop="email">
          <el-input
            prefix-icon="el-icon-message"
            v-model="editForm.email"
            placeholder=""
            style="width:93%"
          ></el-input>
        </el-form-item>
        <el-form-item style="margin-left:150px;margin-right:150px" label="" prop="address">
          <el-input
            prefix-icon="el-icon-map-location"
            v-model="editForm.address"
            placeholder=""
            style="width:93%"
          ></el-input>
        </el-form-item>
        <el-form-item style="margin-left:150px;margin-right:150px" label="" prop="company">
          <el-input
            prefix-icon="el-icon-office-building"
            v-model="editForm.company"
            placeholder=""
            style="width:93%"
          ></el-input>
        </el-form-item>
        <el-form-item style="margin-left:150px;margin-right:150px" label="" prop="department">
          <el-input
            prefix-icon="el-icon-monitor"
            v-model="editForm.department"
            placeholder=""
            style="width:93%"
          ></el-input>
        </el-form-item>
      </el-form>
      <!--  -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editUser()"> </el-button>
      </span>
    </el-dialog>
    <router-view></router-view>

  </div>
</template>
<script>
export default {
  data () {
    //   
    var checkEmail = (rule, value, callback) => {
      // 
      const regEmail = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(.[a-zA-Z0-9_-])+/
      if (regEmail.test(value)) {
        return callback()
      }
      callback(new Error(''))
    }
    //    
    var checkMobile = (rule, value, callback) => {
      // 
      const regMobile = /^1([38][0-9]|4[579]|5[0-3,5-9]|6[6]|7[0135678]|9[89])\d{8}$/
      if (regMobile.test(value)) {
        return callback()
      }
      callback(new Error(''))
    }
    return {
      // 
      queryUserListParams: {
        query: '',
        pageNum: 1,
        pageSize: 10
      },
      // 
      delUserParam: {
        uid: 0,
        flag: true
      },
      // 
      userList: [],
      userTotal: 0,
      //   Dialogd
      addDialogVisible: false,
      //   
      addForm: {
        id: 0,
        username: '',
        password: '',
        mobile: '',
        email: '',
        address: '',
        company: '',
        department: '',
        customerName: ''
      },
      //   
      editForm: {
        username: '',
        mobile: '',
        email: '',
        address: '',
        company: '',
        department: '',
        customerName: ''
      },
      //   
      addFormRules: {
        username: [
          { required: true, message: '', trigger: 'blur' },
          { min: 2, max: 20, message: ' 2  20 ', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '', trigger: 'blur' },
          { min: 4, max: 20, message: ' 4  20 ', trigger: 'blur' }
        ],
        mobile: [
          // { min: 1, max: 12, message: '11', trigger: 'blur' },
          { required: true, message: '', trigger: 'blur' },
          // { type: 'number', message: '11', trigger: 'blur' },
          { validator: checkMobile, trigger: 'blur' }
        ],
        email: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkEmail, trigger: 'blur' }
        ],
        address: [
          {
            min: 2,
            max: 100,
            message: ' 2  10 ',
            trigger: 'blur'
          }
        ],
        company: [
          { min: 2, max: 40, message: ' 2  40 ', trigger: 'blur' }
        ],
        department: [
          { min: 2, max: 20, message: ' 2  20 ', trigger: 'blur' }
        ],
        customerName: [
          { required: true, message: '', trigger: 'blur' },
          { min: 2, max: 20, message: ' 2  20 ', trigger: 'blur' }
        ]
      },
      editDialogVisible: false
    }
  },
  methods: {
    // 
    async getUserList () {
      const { data: response } = await this.$http.get('/queryUser', {
        params: this.queryUserListParams
      })
      if (response.code != 10000) {
        this.$message.error(response.msg)
      } else {
        this.userList = response.data
        this.userTotal = this.userList.length
        // console.log(this.userTotal);
        // console.log(this.userList);
      }
    },
    // 
    async delUser (row) {
      this.delUserParam.uid = row.id
      this.delUserParam.flag = row.del
      const { data: response } = await this.$http.get('/toDelUser', {
        params: this.delUserParam
      })
      if (response.code != 10000) {
        this.$message.error('')
      }
      if (!row.del) {
        this.$message({
          type: 'success',
          message: '!'
        })
      } else {
        this.$message({
          type: 'success',
          message: '!'
        })
      }
      this.getUserList()
    },
    // 
    async changeDel (row) {
      // 
      if (!row.del) {
        await this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        })
          .then(() => {
            this.delUser(row)
            console.log('')
          })
          .catch(() => {
            this.getUserList()
            this.$message({
              type: 'info',
              message: ''
            })
          })
      } else {
        await this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        })
          .then(() => {
            this.delUser(row)
            console.log('')
          })
          .catch(() => {
            this.getUserList()
            this.$message({
              type: 'info',
              message: ''
            })
          })
      }
    },
    // 
    handleSizeChange (newSize) {
      this.queryUserListParams.pageSize = newSize
      this.getUserList()
      // console.log(newSize);
    },
    // 
    handleCurrentChange (newPage) {
      this.queryUserListParams.pageNum = newPage
      this.getUserList()
      // console.log(newPage);
    },
    // 
    closeAdd () {
      this.$refs.addFormRef.resetFields()
    },
    // 
    async addUser () {
      this.$refs.addFormRef.validate(async valid => {
        if (valid) {
          const { data: response } = await this.$http.post(
            '/addUser',
            this.addForm
          )
          if (response.code == 10000) {
            this.$message.success(response.msg)
            this.addDialogVisible = false
            this.getUserList()
          } else {
            this.$message.error(response.msg)
          }
        } else {
        }
      })
    },
    // 
    showDialogVisible (user) {
      this.editForm = user
      this.editDialogVisible = true
    },
    // 
    async editUser () {
      this.$refs.editFormRef.validate(async valid => {
        if (valid) {
          const { data: response } = await this.$http.post(
            '/editUser',
            this.editForm
          )
          if (response.code == 10000) {
            this.$message.success(response.msg)
            this.editDialogVisible = false
            this.getUserList()
          } else {
            this.$message.error(response.msg)
          }
        } else {
        }
      })
    },
    // 
    closeEdit () {
      this.$refs.editFormRef.resetFields()
    },
    // 
    getUserInfo (uid) {
      console.log('')
      this.$alert('...', '...', {
        confirmButtonText: '',
        callback: action => {
          // 
        }
      })
      // 
      // console.log("uid"+uid);
      // this.$router.push('/config/user/userInfo/'+uid);
    }
  },
  created () {
    this.getUserList()
  }
}
</script>
<style lang="stylus" scoped></style>
