<template>
  <div v-loading="loading" element-loading-text="...">
    <!--  -->
    <el-breadcrumb>
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item>{{cid == 'add'? '':''}}</el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <el-row :gutter="10">
        <el-col :span="1.5">
          <el-button @click="goBack()" icon="el-icon-back" type="primary"></el-button>
        </el-col>
      </el-row>
      <!--  -->
      <el-form ref="addCaseFormRef" :rules="addFormRules" :model="addCaseForm" label-width="100px">
        <el-form-item style="margin-left:150px" class="input-center" label="" prop="name">
          <el-input
            prefix-icon="el-icon-edit"
            v-model="addCaseForm.name"
            placeholder=""
            style="width:80%"
          ></el-input>
        </el-form-item>

        <el-form-item style="margin-left:150px" class="input-center" label="" prop="protocolId">
          <el-select
            :disabled="cid != 'add'"
            style="width:80%"
            v-model="addCaseForm.protocolId"
            placeholder=""
          >
            <el-option
              v-for="item in protocolsOptions"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>

        <el-form-item v-if="this.$global.currentProjectId == null && this.$global.currentProjectName == null" style="margin-left:150px" label="" prop="projectId">
          <el-select
            :disabled="cid != 'add'"
            style="width:80%"
            v-model="addCaseForm.projectId"
            placeholder=""
          >
            <el-option
              v-for="item in projectOptions"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>

        <el-form-item  v-else style="margin-left:150px" label="" prop="projectId">
          <el-select
            disabled
            style="width:80%"
            v-model="this.$global.currentProjectName"
            placeholder=""
          >
            <el-option
              v-for="item in projectOptions"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
<!--  -->
        <el-form-item v-if="cid == 'add'" style="margin-left:150px" label="" prop="preCaseGroupId">
          <el-select
            filterable
            @clear="clearSelect()"
            :disabled="addCaseForm.projectId == ''"
            remote
            :remote-method="getCaseGroupList"
            clearable
            style="width:80%"
            v-model="addCaseForm.preCaseGroupId"
            placeholder=""
          >
            <el-option
              v-for="item in caseGroupList"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
          <el-button type="success" icon="el-icon-view" @click="getCaseGroupInfo(addCaseForm.preCaseGroupId)"
           style="margin-left:1%" v-if="addCaseForm.preCaseGroupId != null && addCaseForm.preCaseGroupId != ''"></el-button>
        </el-form-item>

<!--  -->
        <el-form-item v-else style="margin-left:150px" label="" prop="preCaseGroupId">
          <el-select
            filterable
            @clear="clearSelect()"
            disabled
            remote
            :remote-method="getCaseGroupList"
            clearable
            style="width:80%"
            v-model="preCaseGroupName"
            placeholder=""
          >
            <el-option
              v-for="item in caseGroupList"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
          <el-button type="success" icon="el-icon-view" @click="getCaseGroupInfo(addCaseForm.preCaseGroupId)"
           style="margin-left:1%" v-if="addCaseForm.preCaseGroupId != null && addCaseForm.preCaseGroupId != ''"></el-button>
        </el-form-item>

      <el-table stripe  v-if="openCaseListFlag && addCaseForm.preCaseGroupId != null && addCaseForm.preCaseGroupId != ''" border
      :data="caseListTable"
      style="width: 67%;margin-left:250px">
      <!--  -->
      <el-table-column type="expand" >
        <template slot-scope="props">
        <el-form label-position="left" >
          <el-form-item label="" :key="index" v-for="(item,index) in props.row.reference">
            <span style="color:red">{{ item.referenceName}}</span> 
             <span style="color:red">{{ item.referenceDesc}}</span>
          </el-form-item>
        </el-form>
        </template>
      </el-table-column>
      <el-table-column
        prop="pri"
        label=""
        width="100">
      </el-table-column>
      <el-table-column
        prop="name"
        label=""
        width="300">
      </el-table-column>
      <el-table-column
        prop="desc"
        label="">
      </el-table-column>
    </el-table>
        <br/>
        <el-form-item style="margin-left:150px" label="" prop="url">
          <el-input
            :disabled="cid != 'add'"
            prefix-icon="el-icon-discover"
            v-model="addCaseForm.url"
            placeholder=""
            style="width:80%"
          ></el-input>
        </el-form-item>

        <el-form-item
          v-if="addCaseForm.protocolId == 2 "
          style="margin-left:150px"
          label=""
          prop="paramsType"
        >
          <el-select
            :disabled="cid != 'add'"
            style="width:80%"
            v-model="addCaseForm.paramsType"
            placeholder=",jsonform"
          >
            <el-option
              v-for="item in paramsType"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
          <el-button
            style="margin-left:10px"
            v-if="addCaseForm.protocolId == 2 && addCaseForm.paramsType == 2"
            @click="addDomain"
          ></el-button>
        </el-form-item>

        <!--  -->
        <template v-if="addCaseForm.protocolId == 2 && addCaseForm.paramsType == 2">
          <el-form-item
            inline
            style="margin-left:120px"
            v-for="(domain, index) in addCaseForm.domains"
            :label="'' + (index+1)"
            :key="domain.key"
            :prop="'domains.' + index + '.nameValue'"
            :rules="{
      required: true, message: '', trigger: 'blur'
    }"
          >
            <el-input placeholder="" v-model="domain.nameValue" style="width:35%"></el-input>
            <el-input
              placeholder=""
              v-model="domain.paramValue"
              style="width:35%;margin-left:20px;"
            ></el-input>
            <el-button @click.prevent="removeDomain(domain)" style="margin-left:20px;"></el-button>
          </el-form-item>
        </template>

        <el-form-item
          v-if="addCaseForm.protocolId == 2 && addCaseForm.paramsType == 1"
          style="margin-left:150px"
          label=""
          prop="jsonParams"
        >
          <el-input
            type="textarea"
            :rows="5"
            prefix-icon="el-icon-key"
            v-model="addCaseForm.jsonParams"
            placeholder=""
            style="width:80%"
          ></el-input>
        </el-form-item>

        <el-form-item style="margin-left:150px" class="input-center" label="" prop="desc">
          <el-input
            prefix-icon="el-icon-edit"
            v-model="addCaseForm.desc"
            placeholder=""
            style="width:80%"
          ></el-input>
        </el-form-item>
      </el-form>
      <!--  -->
      <div style="text-align:right;margin-right:17%">
        <el-button @click="goBack()"> </el-button>
        <el-button type="primary" @click="getResponse()"> </el-button>
      </div>
    </el-card>

    <!--  -->
    <el-dialog
      :close-on-click-modal="false"
      title=""
      :visible.sync="setAssertCaseDialog"
      width="80%"
      @close="closeEdit()"
    >
      <span></span>
      <!--  -->

      <el-form
        ref="setAssertCaseFormRef"
        :rules="setAssertFormRules"
        :model="setAssertCaseForm"
        label-width="100px"
      >
        <el-form-item style="margin-left:10px;margin-right:10px" label="" prop="response">
          <el-input type="textarea" :rows="5" v-model="responseStr"></el-input>
        </el-form-item>

        <el-form-item label="">
          <el-switch v-model="switchInfo.isAssert"></el-switch>
          <el-button
            type="primary"
            style="margin-left:50px"
            v-if="switchInfo.isAssert"
            @click="addAssertDomain"
          ></el-button>
        </el-form-item>

        <template v-if="switchInfo.isAssert">
          <el-form-item
            inline
            style="margin-left:120px"
            v-for="(domain, index) in setAssertCaseForm.assertDomains"
            :label="'' + (index+1)"
            :key="domain.key"
            :prop="'assertDomains.' + index + '.nameValue'"
            :rules="{
      required: true, message: '', trigger: 'blur'}"
          >
            <el-input placeholder="" v-model="domain.nameValue" style="width:35%"></el-input>
            <el-input
              placeholder=""
              v-model="domain.paramValue"
              style="width:35%;margin-left:10px;"
            ></el-input>
            <el-button
              type="danger"
              @click.prevent="removeAddAssertDomain(domain)"
              style="margin-left:10px;"
            ></el-button>
            <el-button
              type="success"
              @click.prevent="queryAssert(index+1,domain.nameValue)"
              style="margin-left:10px;"
            ></el-button>
          </el-form-item>
          <div style="margin-left:250px;margin-bottom:50px">
            <span style="font-size:20px">
               {{assertResultIndex}} : 
              <span style="color:red">{{assertResult}}</span>
            </span>
          </div>
        </template>

        <el-form-item label="">
          <el-switch v-model="switchInfo.isReference"></el-switch>
          <el-button
            type="primary"
            style="margin-left:50px"
            v-if="switchInfo.isReference"
            @click="addParamsDomain"
          ></el-button>
        </el-form-item>

        <template v-if="switchInfo.isReference">
          <el-form-item
            inline
            style="margin-left:120px"
            v-for="(domain, index) in setAssertCaseForm.paramsDomains"
            :label="'' + (index+1)"
            :key="domain.key"
            :prop="'paramsDomains.' + index + '.nameValue'"
            :rules="{
      required: true, message: '', trigger: 'blur'
    }"
          >
            <el-input placeholder="" v-model="domain.nameValue" style="width:20%"></el-input>
            <el-input
              placeholder=""
              v-model="domain.paramValue"
              style="width:20%;margin-left:10px;"
            ></el-input>
            <el-input
              placeholder=""
              v-model="domain.paramDesc"
              style="width:30%;margin-left:10px;"
            ></el-input>
            <el-button
              type="danger"
              @click.prevent="removeParamsDomain(domain)"
              style="margin-left:10px;"
            ></el-button>
            <el-button
              type="success"
              @click.prevent="queryReference(index+1,domain.nameValue)"
              style="margin-left:10px;"
            ></el-button>
          </el-form-item>
          <div style="margin-left:250px;">
            <span style="font-size:20px">
               {{referenceResultIndex}} : 
              <span style="color:red">{{referenceResult}}</span>
            </span>
          </div>
        </template>
      </el-form>

      <!--  -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="setAssertCaseDialog = false"> </el-button>
        <el-button type="primary" @click="addCase()"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  props: ['cid'],
  data () {
    // JSON
    var checkJson = (rule, value, callback) => {
      if (this.isJSON(value)) {
        return callback()
      }
      callback(new Error('JSON'))
    }
    return {
      // 
      loading: false,
      // 
      textareaHigh: 5,
      // 
      protocolsOptions: [],
      // 
      projectOptions: [],

      addDialogVisible: false,
      //   
      addCaseForm: {
        projectId: '',
        protocolId: '',
        name: '',
        jsonParams: '',
        postParams: '',
        header: '',
        assertFlag: '',
        assertContent: '',
        url: '',
        preComponent: '',
        postComponent: '',
        domains: [
          {
            nameValue: '',
            paramValue: ''
          }
        ],
        paramsType: '',
        assertDomains: [
          {
            nameValue: '',
            paramValue: ''
          }
        ],
        paramsDomains: [
          {
            nameValue: '',
            paramValue: '',
            paramDesc: ''
          }
        ],
        preCaseGroupIndex: ''
      },
      paramsDomains: [
        {
          nameValue: '',
          paramValue: '',
          paramDesc: ''
        }
      ],
      assertDomains: [
        {
          nameValue: '',
          paramValue: ''
        }
      ],
      domains: [
        {
          nameValue: '',
          paramValue: ''
        }
      ],
      // 
      paramsType: [
        {
          id: 1,
          name: 'application/json'
        },
        {
          id: 2,
          name: 'application/x-www-form-urlencoded'
        }
      ],
      //   
      addFormRules: {
        name: [
          { required: true, message: '', trigger: 'blur' },
          { min: 2, max: 40, message: ' 2  20 ', trigger: 'blur' }
        ],
        protocolId: [
          { required: true, message: '', trigger: 'blur' }
        ],
        projectId: [
          { required: true, message: '', trigger: 'blur' }
        ],
        assertFlag: [
          { required: true, message: '', trigger: 'blur' }
        ],
        assertContent: [
          { required: true, message: '', trigger: 'blur' }
        ],
        url: [{ required: true, message: 'URL', trigger: 'blur' }],
        jsonParams: [{ validator: checkJson, trigger: 'blur' }],
        paramsType: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      // 
      setAssertFormRules: {
        // assertDomains:[
        // ],
        // paramsDomains:[
        // ]
      },
      setAssertCaseDialog: false,
      // dialog
      editDialogVisible: false,
      caseGroupList: {},
      queryForm: {
        query: '',
        pageNum: 1,
        pageSize: 50
      },
      responseStr: '',
      setAssertCaseForm: {},
      switchInfo: {
        isAssert: false,
        isReference: false
      },
      assertResult: '',
      assertResultIndex: '',
      referenceResult: '',
      referenceResultIndex: '',
      queryAssertParams: {
        flag: '',
        content: ''
      },
      openCaseListFlag: false,
      caseListTable: [],
      preCaseGroupName: ''
    }
  },
  methods: {

    clearSelect () {
      this.openCaseListFlag = false
    },
    async getCaseGroupInfo (groupId) {
      this.openCaseListFlag = !this.openCaseListFlag
      if (this.openCaseListFlag) {
        const response = await this.$common.get('/getGroupCaseList?groupId=' + groupId)
        this.caseListTable = response.data
      }
    },
    getCaseReferenceParams () {

    },
    // JSON
    isJSON (str) {
      if (typeof str === 'string') {
        try {
          var obj = JSON.parse(str)
          if (typeof obj === 'object' && obj) {
            return true
          } else {
            return false
          }
        } catch (e) {
          console.log('error' + str + '!!!' + e)
          return false
        }
        return true
      }
    },
    async queryAssert (idnex, value) {
      this.queryAssertParams.flag = value
      this.queryAssertParams.content = this.responseStr
      const response = await this.$common.post(
        '/getAssertResult',
        this.queryAssertParams
      )
      if (response.code == 10000) {
        this.assertResult = response.data
        this.assertResultIndex = idnex
      } else {
        this.$message.error('')
      }
    },
    async queryReference (idnex, value) {
      this.queryAssertParams.flag = value
      this.queryAssertParams.content = this.responseStr
      const response = await this.$common.post(
        '/getAssertResult',
        this.queryAssertParams
      )
      if (response.code == 10000) {
        this.referenceResult = response.data
        this.referenceResultIndex = idnex
      }
    },
    async getResponse () {
      this.$refs.addCaseFormRef.validate(async valid => {
        if (valid) {
          this.loading = true
          console.log(this.addCaseForm)
          const response = await this.$common.post(
            '/addSingleCaseGetResponse',
            this.addCaseForm
          )
          if (response.code == 10000) {
            // this.responseStr = response.data.response;
            this.responseStr = this.$common.formatJson(response.data.response)
            this.loading = false
            this.setAssertCaseDialog = true
            this.setAssertCaseForm = response.params
          } else {
            this.$message.error(response.msg)
          }
          this.loading = false
        } else {
          console.log('')
        }
      })
    },
    goBack () {
      // 
      this.$router.push('/case/single')
    },
    async getCaseGroupList (query) {
      this.queryForm.query = query
      this.queryForm.projectId = this.addCaseForm.projectId
      const response = await this.$common.get(
        '/getAllCaseGroup',
        this.queryForm
      )
      this.caseGroupList = response.data
    },
    // form
    removeDomain (item) {
      var index = this.addCaseForm.domains.indexOf(item)
      if (index !== -1) {
        this.addCaseForm.domains.splice(index, 1)
      }
    },
    // form
    addDomain () {
      this.addCaseForm.domains.push({
        nameValue: '',
        paramValue: '',
        key: Date.now()
      })
    },
    addAssertDomain () {
      this.setAssertCaseForm.assertDomains.push({
        nameValue: '',
        paramValue: '',
        key: Date.now()
      })
    },
    addParamsDomain () {
      this.setAssertCaseForm.paramsDomains.push({
        nameValue: '',
        paramValue: '',
        paramDesc: '',
        key: Date.now()
      })
    },
    removeAddAssertDomain (item) {
      var index = this.setAssertCaseForm.assertDomains.indexOf(item)
      if (index !== -1) {
        this.setAssertCaseForm.assertDomains.splice(index, 1)
      }
    },
    removeParamsDomain (item) {
      var index = this.setAssertCaseForm.paramsDomains.indexOf(item)
      if (index !== -1) {
        this.setAssertCaseForm.paramsDomains.splice(index, 1)
      }
    },

    // 
    async addCase () {
      if (this.cid != 'add') {
        // 
        this.$refs.setAssertCaseFormRef.validate(async valid => {
          if (valid) {
            const response = await this.$common.post(
              '/editCase',
              this.setAssertCaseForm
            )
            if (response.code == 10000) {
              this.$message.success(response.msg)
              this.goBack()
            } else {
              this.$message.error(response.msg)
            }
          } else {
            console.log('')
          }
        })
      } else {
        // 
        this.$refs.setAssertCaseFormRef.validate(async valid => {
          if (valid) {
            const response = await this.$common.post(
              '/addCase',
              this.setAssertCaseForm
            )
            if (response.code == 10000) {
              this.$message.success(response.msg)
              this.goBack()
            } else {
              this.$message.error(response.msg)
            }
          } else {
            console.log('')
          }
        })
      }
    },
    closeEdit () {
      this.$refs.setAssertCaseFormRef.resetFields()
      this.textareaHigh = 4
    },
    // 
    async getProtocol () {
      const { data: response } = await this.$http.get('/getAllProtocol')
      this.protocolsOptions = response.data
    },
    // 
    async getProject () {
      this.$common.getAllProject().then(data => {
        this.projectOptions = data
      })
    },
    async getCaseById () {
      if (this.cid != 'add') {
        const response = await this.$common.get('/getCaseById?id=' + this.cid)
        this.addCaseForm = response.data
        this.switchInfo.isAssert = response.total
        this.switchInfo.isReference = response.extend
        if (response.params != null) {
          this.preCaseGroupName = response.params.name
        }
        if (!response.total) {
          this.addCaseForm.assertDomains = this.assertDomains
        }
        if (!response.extend) {
          this.addCaseForm.paramsDomains = this.paramsDomains
        }
      } else {
        console.log('')
      }
    }
  },
  // computed:{
  //   responseStr : function(){
  //     return this.$common.formatJson(this.responseStr);
  //   }
  // },
  created () {
    if (window.sessionStorage.getItem('projectId') != null && window.sessionStorage.getItem('projectName') != null) {
      this.$global.currentProjectId = window.sessionStorage.getItem('projectId')
      this.$global.currentProjectName = window.sessionStorage.getItem('projectName')
      this.addCaseForm.projectId = this.$global.currentProjectId
    }
    this.getProtocol()
    this.getProject()
    this.getCaseById()
  },
  beforeRouteLeave (to, from, next) {
    to.meta.keepAlive = false
    next()
  }
}
</script>
<style lang="less" scoped></style>
