<script>
export default {
  data () {
    return {
      dialogVisible: false,
      title: '',
      form: {
        username: '',
        password: ''
      },
      rules: {
        username: [
          { required: true, message: '', trigger: 'blur' },
          { min: 2, max: 20, message: ' 2  20 ', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '', trigger: 'blur' },
          { min: 4, max: 20, message: ' 4  20 ', trigger: 'blur' }
        ]
      },
      initProjectRules: {
        datasourceUsername: [
          { required: true, message: '', trigger: 'blur' }
        ],
        datasourcePassword: [
          { required: true, message: '', trigger: 'blur' }
        ],
        datasourceUrl: [
          { required: true, message: '', trigger: 'blur' }
        ],
        serverPort: [
          { required: true, message: '', trigger: 'blur' }
        ],
        loginUsername: [
          { required: true, message: '', trigger: 'blur' },
          { min: 6, max: 20, message: ' 6  20 ', trigger: 'blur' }
        ],
        loginPassword: [
          { required: true, message: '', trigger: 'blur' },
          { min: 6, max: 20, message: ' 6  20 ', trigger: 'blur' }
        ]
        // redisHost: [
        // ],
        // redisPort: [
        // ],
        // redisPassword: [
        // ]

      },
      ymlConfig: {
        datasourceUsername: '',
        datasourcePassword: '',
        datasourceUrl: '',
        loginUsername: '',
        loginPassword: ''
        // redisHost:"",
        // redisPort:"",
        // redisPassword:""
      }
    }
  },
  methods: {

    reset () {
      // form
      this.$refs.loginFormRef.resetFields()
    },
    openInitProjectDialog () {
      this.dialogVisible = true
    },
    async createYml () {
      this.$refs.createYmlRef.validate(async valid => {
        if (valid) {
          const response = await this.$common.post('/createYmlConfig', this.ymlConfig)
          console.log(response)
          if (response.code === 10000) {
            this.$message.success('30s')
            this.dialogVisible = false
          } else {
            this.$message.error('Ken!')
          }
        } else {
          return false
        }
      })
    },
    resetYmlConfig () {
      this.$refs.createYmlRef.resetFields()
    },
    login () {
      this.$refs.loginFormRef.validate(async valid => {
        if (valid) {
          const result = await this.$http.post('/login', this.form)
          // console.log(result);
          const data = result.data
          if (data.code == 10000) {
            this.$message.success(data.msg)
            // console.log("token  " + data.data.id);
            window.localStorage.setItem('token', data.extend)
            window.localStorage.setItem('customerName', data.data.customerName)
            this.$router.push('/home')
          } else {
            this.$message.error(data.msg)
          }
        } else {
        }
      })
    },
    register () {
      this.$message(',')
    },
    handleClose () {
      this.resetYmlConfig()
      this.dialogVisible = false
    }
  }
}
</script>
