      </el-table>

      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryDataSourceListParams.pageNum"
        :page-sizes="[5, 10, 20, 50]"
        :page-size="queryDataSourceListParams.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="dataSourceTotal"
      ></el-pagination>
    </el-card>

    <!--  -->
    <el-dialog :close-on-click-modal="false" title="" :visible.sync="addDataSourceDialogVisible" width="50%" @close="closeAddFirst()">
      <span></span>
      <!--  -->
      <el-form ref="addDataSourceFormRef" :rules="addDataSourceFormRules" :model="addDataSourceForm" label-width="100px">
        <el-form-item
          style="margin-left:150px;margin-right:150px"
          class="input-center"
          label=""
          prop="name"
        >
          <el-input
            prefix-icon="el-icon-edit"
            v-model="addDataSourceForm.name"
            placeholder=""
            style="width:93%"
          ></el-input>
        </el-form-item>

        <el-form-item
          style="margin-left:150px;margin-right:150px"
          class="input-center"
