        >
        <el-select style="width:93%" v-model="addDataSourceForm.projectId" placeholder="">
            <el-option
              v-for="item in projectOptions"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>

        <el-form-item v-else
          style="margin-left:150px;margin-right:150px"
          class="input-center"
          label=""
          prop="projectId"
        >
        <el-select disabled style="width:93%" v-model="this.$global.currentProjectName" placeholder="">
            <el-option
              v-for="item in projectOptions"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>

        <el-form-item style="margin-left:150px;margin-right:150px" label="DB" prop="url">
          <el-input type="textarea" :rows="5"
            prefix-icon="el-icon-key"
            v-model="addDataSourceForm.url"
            placeholder="DB"
            style="width:93%"
          ></el-input>
        </el-form-item>

        <el-form-item style="margin-left:150px;margin-right:150px" label="" prop="user">
          <el-input
            prefix-icon="el-icon-key"
