            placeholder=""
            style="width:93%"
          ></el-input>
        </el-form-item>

        <el-form-item style="margin-left:150px;margin-right:150px" label="" prop="password">
          <el-input
          type="password"
            prefix-icon="el-icon-key"
            v-model="addDataSourceForm.password"
            placeholder=""
            style="width:93%"
          ></el-input>
        </el-form-item>

      </el-form>
      <!--  -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDataSourceDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addDataSource()"> </el-button>
      </span>
    </el-dialog>

